.our-team-one {
    .container {}
    &__wrap {
        padding: 174px 0 124px 0;
    }
    .section-common-title {
        display: flex;
        flex-wrap: wrap;
        gap: 0 68px;
        padding-bottom: 56px;
    }
    &__chose-reasone {
        width: 100%;
    }
    &__chose-name { margin-left: 0;}
    &__title {
        width: calc(calc(58.6% - 34px) / 1);
        margin-bottom: 0;
    }
    &__text {
        width: calc(calc(41.4% - 34px) / 1);
        font-size: 16px;
        line-height: 32px;
        padding-right: 0;
        padding-top: 7px;
        margin-bottom: 0;
    }

    &__bx {}
    &__col {}
    &__inner {}
    &__img {}
    &__info-bx {}
    &__social {}
    &__name {}
    &__designation {}
    &__plus {}
    &__plus-icon {}
}